 import { userLogin } from '../../api/api';
 import { isLogin } from '../../libs/authService.js';
	export default {
		data() {
			return {
				username: '',
				password: ''
			}
		},
		onLoad() {
			
		},
		onUnload() {

		},
		methods: {
			login() {
				userLogin({
					username: this.username,
					password: this.password
				}).then(res => {
					// token全局写入
					uni.setStorage({
						key: "token",
						data: res.data
					})
					// 成功提示
					uni.showToast({
						title: '登录成功!',
						duration: 2000
					})
					// 延迟跳转
					setTimeout(() => {
						uni.switchTab({
							url:"../home/home"
						})
					}, 2000)
				})
			}
		}
	}
